Drupal.behaviors.TeamListing = {
  attach(context) {
    const breakpoint = window.matchMedia('(max-width: 649px)');
    const cardLimit = 4;

    const cards = Array.from(context.querySelectorAll('.team-listing .team-listing__cards .card-person'));

    const showMoreEl = context.querySelector('.team-listing .team-listing__show-more');

    const overlayEl = context.querySelector('.team-listing .team-listing__show-more .overlay');

    const buttonShowMore = context.querySelector('.team-listing .team-listing__show-more #btn-show-more');

    function handleToggleCards() {
      if (cards.length > cardLimit) {
        showMoreEl.classList.add('visible');

        for (let i = cards.length - 1; i >= cardLimit; i -= 1) {
          cards[Number(i)].style.display = 'none';
          buttonShowMore.textContent = '+';
        }
        let isShowMore = false;

        buttonShowMore?.addEventListener('click', () => {
          if (!isShowMore) {
            for (let i = cards.length - 1; i >= cardLimit; i -= 1) {
              cards[Number(i)].style.display = 'block';
            }

            buttonShowMore.textContent = 'âˆ’';
            overlayEl.style.display = 'none';
            isShowMore = true;
          } else {
            for (let i = cards.length - 1; i >= cardLimit; i -= 1) {
              cards[Number(i)].style.display = 'none';
            }

            buttonShowMore.textContent = '+';
            overlayEl.style.display = 'block';
            isShowMore = false;
          }
        });
      }
    }
    function breakpointCheck() {
      if (breakpoint.matches === true) {
        handleToggleCards();
      } else {
        showMoreEl?.classList.remove('visible');
        for (let i = cards.length - 1; i >= cardLimit; i -= 1) {
          cards[Number(i)].style.display = '';
        }
      }
    }

    breakpoint.addEventListener('change', breakpointCheck);

    breakpointCheck();
  },
};
